/**
 * NPC - 
 * NPC
 */

export interface NpcCharacter {
  id: string;
  name: string; // 
  dbKey: string; // enemyDatabasekey
  skillKey: string; // enemySkillDatabasekey
  portraitKey?: string; // name
  gender?: '' | '' | ''; // 
  level: number; // 
  category: NpcCategory; // 
  description: string; // 
}

export type NpcCategory =
  | ''
  | ''
  | 'BF'
  | ''
  | ''
  | ''
  | ''
  | ''
  | ''
  | ''
  | 'S'
  | 'A'
  | 'B'
  | 'C'
  | 'D'
  | '';

/**
 * URL
 * @param name 
 * @param portraitKey 
 */
export function getNpcPortraitUrl(name: string, portraitKey?: string): string {
  const fileName = portraitKey || name;
  return `https://raw.githubusercontent.com/vincentrong2005/Fatria/main////${encodeURIComponent(fileName)}.png`;
}

